import * as React from 'react';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import { rpaData } from '../../state/rpaData';
import { useRecoilState } from "recoil";

export default function TiposDeServico() {
  const [data, setData] = useRecoilState(rpaData)

  const setServico = (e) => {
    setData(data => {
      let servico = [...data.servico]
      servico = e.target.value
      return { ...data, servico }
    })
  }


  return (
    <FormControl >
      <FormLabel sx={{ margin: '2rem 0 .5rem 0' }} id="demo-radio-buttons-group-label">Serviços Disponíveis</FormLabel>
      <RadioGroup
        data-radio_group
        aria-labelledby="demo-radio-buttons-group-label"
        value={data.servico}
        name="radio-buttons-group"
        onChange={setServico}
      >
        <FormControlLabel value="ACOR" control={<Radio />} label="Assinar Acórdãos" />
        <FormControlLabel value="IRPA" control={<Radio />} label="Inserir e Assinar Relatórios" />
        <FormControlLabel value="VOTO" control={<Radio />} label="Incluir Voto no Sistema Sessão Virtual" />
      </RadioGroup>
    </FormControl>
  );
}