import * as React from 'react';
import Box from '@mui/material/Box';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import TiposDeServico from '../TiposDeServico';
import { rpaData } from '../../state/rpaData';
import { useRecoilState } from "recoil";
import FormRelatorios from '../FormRelatorios';
import FormAcordaos from '../FormAcordaos';
import CodeGenerator from '../CodeGenerator';
import copy from "copy-to-clipboard";
import Divider from '@mui/material/Divider';

import { FormatAlignCenter } from '@mui/icons-material';
import FormVoto from '../FormVoto';
import generateAcordaosCode from '../../js/codeGenerator';
import AlertaSucesso from '../AlertaSucesso';

const steps = ['Selecione o Serviço que o robô fará'];

export default function Form() {

  //snackbar functions {

  const [open, setOpen] = React.useState(false);

  const handleClick = () => {
    setOpen(true);
  };

  //snackbar functions }

  const [activeStep, setActiveStep] = React.useState(0);
  const [data, setData] = useRecoilState(rpaData)
  const [skipped, setSkipped] = React.useState(new Set());

  const isStepOptional = (step) => {
    return step === null;
  };

  const isStepSkipped = (step) => {
    return skipped.has(step);
  };

  const handleNext = () => {
    let newSkipped = skipped;
    if (isStepSkipped(activeStep)) {
      newSkipped = new Set(newSkipped.values());
      newSkipped.delete(activeStep);
    }

    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    setSkipped(newSkipped);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleSkip = () => {
    if (!isStepOptional(activeStep)) {
      // You probably want to guard against something like this,
      // it should never occur unless someone's actively trying to break something.
      throw new Error("You can't skip a step that isn't optional.");
    }

    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    setSkipped((prevSkipped) => {
      const newSkipped = new Set(prevSkipped.values());
      newSkipped.add(activeStep);
      return newSkipped;
    });
  };

  const handleReset = () => {
    setActiveStep(0);

  };


  function copyCode() {
    let codigoCompleto = [...data.codigoCompleto]
    codigoCompleto = generateAcordaosCode(data)
    copy(codigoCompleto)
    handleClick()
  }

  return (
    <>
      <Typography sx={{ marginTop: '1rem', color: '#1976d2' }} align='center' variant='h3'>Gerador de Códigos RPA</Typography>

      <Box sx={{ width: '100vw', display: 'flex', justifyContent: 'center' }}>
        <Box sx={{ width: '80vw', maxWidth: '1100px', border: '1px solid #999', marginTop: '2rem', padding: '16px', borderRadius: '16px' }}>
          <Stepper activeStep={activeStep}>
            {steps.map((label, index) => {
              const stepProps = {};
              const labelProps = {};
              if (isStepOptional(index)) {
                labelProps.optional = (
                  <Typography variant="caption">Optional</Typography>
                );
              }
              if (isStepSkipped(index)) {
                stepProps.completed = false;
              }
              return (
                <Step key={label} {...stepProps}>
                  <StepLabel {...labelProps}>{label}</StepLabel>
                </Step>
              );
            })}
          </Stepper>
          {/* se for o último step */}
          {activeStep === steps.length ? (
            <React.Fragment>
              <Typography sx={{ mt: 2, mb: 1 }}>
                All steps completed - you&apos;re finished
              </Typography>
              <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
                <Box sx={{ flex: '1 1 auto' }} />
                <Button onClick={handleReset}>Reset</Button>
              </Box>
            </React.Fragment>
          ) : (
            <React.Fragment>
              {activeStep === 0 && (<TiposDeServico />)}

              <Divider>Selecione os Parâmetros</Divider>
              <AlertaSucesso open={open} setOpen={setOpen} />

              {(activeStep === 0 && data.servico === 'ACOR') && (<FormAcordaos />)}
              {(activeStep === 0 && data.servico === 'IRPA') && (<FormRelatorios />)}
              {(activeStep === 0 && data.servico === 'VOTO') && (<FormVoto />)}

              {activeStep === 2 && (<CodeGenerator />)}


              <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
                <Button
                  color="inherit"
                  disabled={activeStep === 0}
                  onClick={handleBack}
                  sx={{ mr: 1 }}
                >
                  Voltar
                </Button>
                <Box sx={{ flex: '1 1 auto' }} />

                {isStepOptional(activeStep) && (
                  <Button color="inherit" onClick={handleSkip} sx={{ mr: 1 }}>
                    Skip
                  </Button>
                )}

                {activeStep === steps.length - 1 ?
                  <Button onClick={copyCode}>
                    Copiar Código
                  </Button> :

                  <Button onClick={handleNext}>
                    Avançar
                  </Button>}
              </Box>
            </React.Fragment>
          )}
        </Box>
      </Box></>
  );
}