import * as React from 'react';
import { rpaData } from '../../state/rpaData';
import { useRecoilState } from "recoil";

import Chip from '@mui/material/Chip';
import Stack from '@mui/material/Stack';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';

import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import { Typography } from '@mui/material';
import generateAcordaosCode from '../../js/codeGenerator';

// eslint-disable-next-line import/no-anonymous-default-export
export default function () {
  const [data, setData] = useRecoilState(rpaData)

  const setNumeroProcesso = (e) => {
    setData(data => {
      let numeroProcesso = [...data.numeroProcesso]
      numeroProcesso = e.target.value
      return { ...data, numeroProcesso }
    })
  }

  React.useEffect(() => {
    console.log(data)
  }, [data])

  const handleChangeNumSessao = (e) => {
    setData(data => {
      let numeroSessao = [...data.numeroSessao]
      numeroSessao = e.target.value
      numeroSessao.replace('S', '')
      console.log(numeroSessao)
      return { ...data, numeroSessao }
    })

  };

  const handleChangeNumTri = (e) => {
    setData(data => {
      let numeroTrimestre = [...data.numeroTrimestre]
      numeroTrimestre = e.target.value
      return { ...data, numeroTrimestre }
    })
  };

  React.useEffect(e => {
    setData(data => {

      let codigoCompleto = [...data.codigoCompleto]
      codigoCompleto = generateAcordaosCode(data)
      return { ...data, codigoCompleto }
    })
  }, [])

  const handle_change_session_name = (e) => {
    setData(data => {
      let session_name = [...data.session_name]
      session_name = e.target.value
      return { ...data, session_name }
    })
  };

  return (
    <Box>
      <Box
        component="form"
        sx={{
          '& > :not(style)': { m: 1, width: '25ch' }, display: 'flex', margin: '0'
        }}
        noValidate
        autoComplete="off"
      >
        <Box sx={{ height: '10.41vw', display: 'flex', flexDirection: 'column', justifyContent: 'space-around' }}>
          <Typography >Cole o Número do Processo</Typography>
          <TextField onChange={setNumeroProcesso} id="filled-basic" label="Número do Processo" value={data.numeroProcesso} variant="filled" />
        </Box>

        <Box
          sx={{ height: '10.41vw', display: 'flex', flexDirection: 'column', justifyContent: 'space-around' }}>
          <InputLabel id="demo-simple-select-label">Nome da Sessão</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={data.session_name}
            label="Nome da Sessão"
            onChange={handle_change_session_name}
          >
            <MenuItem value={'CRIMINAL'}>Câmara Criminal</MenuItem>
            <MenuItem value={'CÍVEL'}>Câmara Cível</MenuItem>
            <MenuItem value={'ADMINISTRATIVOS'}>Procedimentos Administrativos</MenuItem>
            <MenuItem value={'PLENO'}>Tribunal Pleno</MenuItem>
            <MenuItem value={'REUNIDAS'}>Câmaras Reunidas</MenuItem>
            <MenuItem value={'RECURSAL'}>Turma Recursal</MenuItem>
          </Select>
        </Box>

        <Box
          sx={{ height: '10.41vw', display: 'flex', flexDirection: 'column', justifyContent: 'space-around' }}>
          <InputLabel id="demo-simple-select-label">Número do Trimestre</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={data.numeroTrimestre}
            label="Número do Trimestre"
            onChange={handleChangeNumTri}
          >
            <MenuItem value={'T1'}> 1° Trimestre</MenuItem>
            <MenuItem value={'T2'}> 2° Trimestre</MenuItem>
            <MenuItem value={'T3'}> 3° Trimestre</MenuItem>
            <MenuItem value={'T4'}> 4° Trimestre</MenuItem>
          </Select>
        </Box>
        <Box sx={{ height: '10.41vw', display: 'flex', flexDirection: 'column', justifyContent: 'space-around' }}>
          <Typography >Número da Sessão</Typography>
          <TextField type={'number'} onChange={handleChangeNumSessao} id="filled-basic" label="Número da Sessão" value={data.numeroSessao} variant="filled" />
        </Box>
      </Box>

      <Stack sx={{ margin: '2rem 1rem 1rem 1rem' }} direction="row" spacing={1}>
        <Chip label={generateAcordaosCode(data)} />
      </Stack>


    </Box>
  );
}