export default function generateAcordaosCode(data) {
  let code;

  data.servico === "ACOR" &&
    (code = "RPA://" + data.servico + "/" + data.numeroProcesso + "//");

  data.servico === "IRPA" &&
    (code =
      "RPA://" +
      data.servico +
      "/" +
      data.numeroProcesso +
      "/" +
      data.tipoDocumento +
      "/" +
      data.tipoMovimentacao +
      "/" +
      data.codigoUsuario +
      "//");

  data.servico === "VOTO" &&
    (code =
      "RPA://" +
      data.servico +
      "/" +
      data.session_name +
      "/" +
      data.numeroProcesso +
      "/" +
      data.numeroTrimestre +
      "/S" +
      data.numeroSessao +
      "//");
  return code;
}
