/* eslint-disable react-hooks/exhaustive-deps */
import * as React from 'react';
import { rpaData } from '../../state/rpaData';
import { useRecoilState } from "recoil";


import Chip from '@mui/material/Chip';
import Stack from '@mui/material/Stack';

import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';

import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import { Typography } from '@mui/material';

// eslint-disable-next-line import/no-anonymous-default-export
export default function () {
  const [data, setData] = useRecoilState(rpaData)

  const setNumeroProcesso = (e) => {
    setData(data => {
      let numeroProcesso = [...data.numeroProcesso]
      numeroProcesso = e.target.value
      return { ...data, numeroProcesso }
    })
  }


  const handleChangeTipoDoc = (e) => {
    setData(data => {
      let tipoDocumento = [...data.tipoDocumento]
      tipoDocumento = e.target.value
      return { ...data, tipoDocumento }
    })
  };

  const handleChangeTipoMov = (e) => {
    setData(data => {
      let tipoMovimentacao = [...data.tipoMovimentacao]
      tipoMovimentacao = e.target.value
      return { ...data, tipoMovimentacao }
    })
  };

  const handleChangeCodeUser = (e) => {
    setData(data => {
      let codigoUsuario = [...data.codigoUsuario]
      codigoUsuario = e.target.value
      return { ...data, codigoUsuario }
    })
  };

 

  function generateAcordaosCode(data) {
    let code;

    data.servico === 'ACOR' && (code = 'RPA://' + data.servico + "/" + data.numeroProcesso + '//')

    data.servico === 'IRPA' && (code = 'RPA://' + data.servico + "/" + data.numeroProcesso + '/' + data.tipoDocumento + '/' + data.tipoMovimentacao + '/' + data.codigoUsuario + '//')

    data.servico === 'VOTO' && (code = 'RPA://' + data.servico + "/" + data.numeroProcesso + '/' + data.numeroTrimestre + '/' + data.numeroSessao + '//')
    return code
  }

  return (
    <Box>
      <Box component="form"
        sx={{
          '& > :not(style)': { m: 1, width: '25ch' }, display: 'flex', margin: '0'
        }}
        noValidate
        autoComplete="off">
        <Box sx={{ height: '10.41vw', display: 'flex', flexDirection: 'column', justifyContent: 'space-around' }}>
          <Typography >Cole o Número do Processo</Typography>
          <TextField onChange={setNumeroProcesso} id="filled-basic" label="Número do Processo" value={data.numeroProcesso} variant="filled" />
        </Box>

        <Box
          sx={{ height: '10.41vw', display: 'flex', flexDirection: 'column', justifyContent: 'space-around' }}>
          <InputLabel id="demo-simple-select-label">Tipo de Documento</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={data.tipoDocumento}
            label="Tipo de Documento"
            onChange={handleChangeTipoDoc}
          >
            <MenuItem value={'DESP'}>Despacho</MenuItem>
            <MenuItem value={'DECI'}>Decisão</MenuItem>
            <MenuItem value={'JULG'}>julgamento</MenuItem>
          </Select>
        </Box>

        <Box sx={{ height: '10.41vw', display: 'flex', flexDirection: 'column', justifyContent: 'space-around' }}>
          <InputLabel id="demo-simple-select-label">Tipo de Movimentação</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={data.tipoMovimentacao}
            label="Tipo de Movimentação"
            onChange={handleChangeTipoMov}
          >
            <MenuItem value={'11878'}>11878</MenuItem>
            <MenuItem value={'11010'}>11010</MenuItem>
            <MenuItem value={'12313'}>12313</MenuItem>
          </Select>
        </Box>

        <Box sx={{ height: '10.41vw', display: 'flex', flexDirection: 'column', justifyContent: 'space-around' }}>
          <InputLabel id="demo-simple-select-label">Código do Usuário</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={data.codigoUsuario}
            label="Código do Usuário"
            onChange={handleChangeCodeUser}
          >
            <MenuItem value={'A03'}>Adenilson - A03</MenuItem>
            <MenuItem value={'A01'}>Tavares - A01</MenuItem>
            <MenuItem value={'M02'}>Juiz Esdras - M02</MenuItem>
          </Select>
        </Box>


      </Box>
      <Stack sx={{ margin: '2rem 1rem 1rem 1rem' }} direction="row" spacing={1}>
        <Chip label={generateAcordaosCode(data)} />
      </Stack>





    </Box>
  );
}